<template>
  <div>
    <h1>this is 外卖</h1>
    <v-back></v-back><v-search></v-search>
    <ul>
      <li>router-link跳转</li>
      <li
        class="item"
        is="router-link"
        :to="'/foodDetail?id=' + item.id + item.name + item.price"
        v-for="item in foods"
        :key="item.id"
      >
        <img :src="item.imgs" alt="" />
        <div class="name">
          <p>电影名字:{{ item.name }}</p>
          <p>价格:<span>$</span>{{ item.price }}</p>
          <p class="p2">送到时间:{{ item.timer | filterTime }}</p>
        </div>
      </li>
    </ul>
    <br />
    <hr />
    <br />
    <ul>
      <li>$router.push跳转</li>
      <li
        class="item"
        v-for="item in foods"
        :key="item.id"
        @click="
          $router.push('/foodDetail?id=' + item.id + item.name + item.price)
        "
      >
        <img :src="item.imgs" alt="" />
        <div class="name">
          <p>电影名字:{{ item.name }}</p>
          <p>价格:<span>$</span>{{ item.price }}</p>
          <p class="p2">送到时间:{{ item.timer | filterTime }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  // 组件进来之前
  beforeRouteEnter(to, from, next) {
    //  beforeRouteEnter比组件进来的早
    console.log(this); //undefined
    if (from.path == "/index/home" || from.path.includes("/foodDetail")) {
      next();
    } else {
      next("/index/home");
    }
  },
  beforeRouteLeave(to, from, next) {
    next();
    console.log("外卖离开了");
  },
  beforeRouteUpdate(to, from, next) {
    console.log("组件更新了");
    console.log(to);
    next();
  },
  data() {
    return {
      foods: [
        {
          id: 11,
          name: "张亮麻辣烫",
          imgs:
            "https://ns-strategy.cdn.bcebos.com/ns-strategy/upload/fc_big_pic/part-00054-1690.jpg",
          price: 12.99,
          timer: 1615532263457,
        },
        {
          id: 12,
          name: "杨国福麻辣烫",
          imgs:
            "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1342554855,3895200018&fm=26&gp=0.jpg",
          price: 11.99,
          timer: 1613532263457,
        },
        {
          id: 13,
          name: "大盘鸡",
          imgs:
            "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3218679926,1640336748&fm=26&gp=0.jpg",
          price: 18.99,
          timer: 1515532263457,
        },
        {
          id: 14,
          name: "海底捞",
          imgs:
            "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1418326076,3232376317&fm=26&gp=0.jpg",
          price: 238.99,
          timer: 2615532263457,
        },
      ],
    };
  },
};
</script>

<style scoped>
h1 {
  text-align: center;
  padding: 10px 0 0 0;
}
ul .item {
  width: 100vw;
  height: 2.5rem;

  margin-top: 0.2rem;
  display: flex;
  background: #f6f6f6;
}
ul .item img {
  width: 2rem;
  height: 2.5rem;
}
ul .item .name {
  flex: 1;
}
ul .item .name p {
  width: 1.5rem;
  line-height: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top: 0.5rem;
  padding: 0 0.2rem;
}
ul .item .name p:nth-child(1) {
  color: skyblue;
}
ul .item .name p span {
  color: red;
}
.p2 {
  font-size: 0.14rem;
  color: rgb(0, 255, 13);
}
</style>


